import React from "react";
import "./faceBoxes.css";

const FaceBoxes = ({ regionData }) => {
  if (regionData.length > 0) {
    const image = document.getElementById("inputimage");
    const width = image.width;
    const height = image.height;

    const output = regionData.map((regionID) => {
      var leftCol = regionID.region_info.bounding_box.left_col * width;
      var topRow = regionID.region_info.bounding_box.top_row * height;
      var rightCol =
        width - regionID.region_info.bounding_box.right_col * width;
      var bottomRow =
        height - regionID.region_info.bounding_box.bottom_row * height;
      console.log(regionID);
      return (
        <div
          className="face-box"
          style={{
            left: leftCol,
            right: rightCol,
            top: topRow,
            bottom: bottomRow,
          }}
        >
          <div className="value-label">{regionID.value}</div>
        </div>
      );
    });
    return output;
  }

  return <div></div>;
};

export default FaceBoxes;
