import React from "react";
import Navigation from "./components/navigation/navigation.js";
import Logo from "./components/logo/logo.js";
import ImageLinkForm from "./components/imageLinkForm/imageLinkForm.js";
import FaceRecognition from "./components/faceRecognition/faceRecognition.js";
import Rank from "./components/rank/rank.js";
import Particles from "react-particles-js";
import "./App.css";
import Clarifai from "clarifai";

const particlesOptions = {
  particles: {
    number: {
      value: 100,
      density: {
        enable: true,
        value_area: 800,
      },
    },
    size: {
      value: 2,
    },
    move: {
      speed: 2,
      out_mode: "out",
    },
  },
  interactivity: {
    events: {
      onhover: {
        enable: true,
        mode: "bubble",
      },
    },
    modes: {
      bubble: {
        distance: 400,
        size: 5,
        duration: 2,
        opacity: 5,
        speed: 10,
      },
    },
  },
};

const app = new Clarifai.App({
  apiKey: "3ccb75a61e104c53b8d3d06047bc0008",
});

class App extends React.Component {
  constructor() {
    super();
    this.input = "";
    this.state = {
      imageUrl: "",
      regions: {},
    };
  }

  setFaceLocations = (data) => {
    console.log("data", data);
    const clarifaiFaceRegions = data.outputs[0].data.regions;
    this.setState({ regions: clarifaiFaceRegions });
    // const image = document.getElementById("inputimage");
    // const width = Number(image.width);
    // const height = Number(image.height);
    // return {
    //   leftCol: clarifaiFace.left_col * width,
    //   topRow: clarifaiFace.top_row * height,
    //   rightCol: width - clarifaiFace.right_col * width,
    //   bottomRow: height - clarifaiFace.bottom_row * height,
  };

  onInputChange = (event) => {
    this.input = event.target.value;
    console.log(this.input);
  };

  onSubmit = () => {
    this.setState({
      imageUrl: this.input,
    });
    app.models
      .predict(Clarifai.FACE_DETECT_MODEL, this.input)
      .then((response) => this.setFaceLocations(response))
      .catch((error) => console.log(error));
  };

  render() {
    return (
      <div className="App">
        <Particles className="particles" params={particlesOptions} />
        <Navigation />
        <Logo />
        <Rank />
        <ImageLinkForm
          onInputChange={this.onInputChange}
          onSubmit={this.onSubmit}
        />
        <FaceRecognition
          imageUrl={this.state.imageUrl}
          regionData={this.state.regions}
        />
      </div>
    );
  }
}

export default App;
