import React from "react";
import FaceBoxes from "./faceBoxes.js";

const FaceRecognition = ({ imageUrl, regionData }) => {
  return (
    <div className="center">
      <div className="absolute mt2">
        <img
          alt=""
          src={imageUrl}
          width="800px"
          height="auto"
          id="inputimage"
        ></img>
        <FaceBoxes regionData={regionData} />
      </div>
    </div>
  );
};

export default FaceRecognition;
